@font-face {
   font-family: 'material-ketchup';
   src: url('#{$fonts_path}/material-ketchup.eot?sx0fx9');
   src: url('#{$fonts_path}/material-ketchup.eot?sx0fx9#iefix') format('embedded-opentype'),
   url('#{$fonts_path}/material-ketchup.ttf?sx0fx9') format('truetype'),
   url('#{$fonts_path}/material-ketchup.ttf?sx0fx9') format('woff'),
   url('#{$fonts_path}/material-ketchup.svg?sx0fx9#material-ketchup') format('svg');
   font-weight: normal;
   font-style: normal;
}

%icon-font-family {
   /* use !important to prevent issues with browser extensions that change fonts */
   font-family: 'material-ketchup' !important;
   speak: none;
   font-style: normal;
   font-weight: normal;
   font-variant: normal;
   text-transform: none;
   line-height: 1; //vertical-align: middle;
   font-size: 1rem;
   /* Better Font Rendering =========== */
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
}

